import AddCircleOutlineRoundedIcon from "@mui/icons-material/AddCircleOutlineRounded";
import {
  Box,
  DialogActions,
  DialogContent,
  IconButton,
  Stack,
  TextField,
} from "@mui/material";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Typography from "@mui/material/Typography";
import React, { useState, useRef } from "react";
import GridFactory from "../../../Tools/GridFactory";
import { StextField } from "../../../Tools/StyledComp";
import CloseIcon from "@mui/icons-material/Close";

export default function AddTourDialog({ CloseDialog, addDialog }) {
  const [Cities, setCities] = useState([{}]);
  const [Tname, setTname] = useState("");
  const [Cname, setCname] = useState("");
  const ref = useRef();
  const AddCitie = () => {
    setCities([...Cities, { cname: Cname, startd: "", endd: "" }]);
    setCname("");
    ref.current.focus();
  };
  const HandleChange = (e) => {
    if (e.target.name === "Tname") setTname(e.target.value);
    else setCname(e.target.value);
  };
  return (
    <Dialog
      onClose={CloseDialog}
      open={addDialog}
      maxWidth="md"
      fullWidth
      sx={classes.Dialog}
    >
      <DialogTitle>
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
        >
          <Typography sx={classes.DialogTitle}>Add Toure</Typography>
          <IconButton onClick={CloseDialog}>
            <CloseIcon />
          </IconButton>
        </Stack>
      </DialogTitle>
      <DialogContent sx={{ p: 0 }}>
        <List>
          <ListItem>
            <StextField
              onChange={HandleChange}
              value={Tname}
              color="secondary"
              variant="outlined"
              name="Tname"
              label="Toure Name: *"
              fullWidth
            />
          </ListItem>
          <ListItem>
            <Stack
              direction="row"
              spacing={2}
              sx={{ width: "100%" }}
              alignItems="center"
            >
              <StextField
                inputRef={ref}
                color="secondary"
                variant="outlined"
                label="City Name: *"
                name="Cname"
                onChange={HandleChange}
                fullWidth
                value={Cname}
                sx={{ flexGrow: 1 }}
              />
              <IconButton color="success" onClick={AddCitie}>
                <AddCircleOutlineRoundedIcon />
              </IconButton>
            </Stack>
          </ListItem>
        </List>
        <CitiesTable Cities={Cities} />
      </DialogContent>
      <DialogActions sx={{ p: 2 }}>
        <Button color="success" variant="contained">
          Submit
        </Button>
      </DialogActions>
    </Dialog>
  );
}

const CitiesTable = ({ Cities }) => {
  return (
    <Box>
      <HeadTable />
      <BodyTable Cities={Cities} />
    </Box>
  );
};

const HeadTable = () => {
  return (
    <Box sx={classes.HeadBox}>
      <GridFactory
        spacing={2}
        items={items}
        gridSize={gridSize}
        child={<TypoHead />}
      />
    </Box>
  );
};

const BodyTable = ({ Cities }) => {
  return (
    <Box sx={classes.BodyBox}>
      {Cities.map((city, index) => (
        <GridFactory
          spacing={2}
          items={items}
          values={city}
          gridSize={gridSize}
          child={<TypoBody />}
        />
      ))}
    </Box>
  );
};

const TypoBody = ({item,  values }) => {
  return <Typography sx={classes.Title}>{values[item.value]}</Typography>;
};

const TypoHead = ({ item }) => {
  return <Typography sx={classes.Title}>{item.label}</Typography>;
};

const gridSize = {
  xs: 3,
  sm: 3,
  md: 3,
  lg: 3,
};

const items = [
  { label: "City Name", value: "cname" },
  { label: "Start Date", value: "startd" },
  { label: "End Date", value: "endd" },
  { label: "Actions", value: undefined },
];

const classes = {
  DialogTitle: {
    fontFamily: "Roboto",
    textTransform: "uppercase",
    fontWeight: "300",
    color: "secondary.main",
  },
  HeadBox: {
    m: 2,
    padding: "16px",
    backgroundColor: "#ededed52",
    borderRadius: "12px",
    outline: "1px solid",
    outlineColor: "#20AAE24f",
  },
  BodyBox: {
    m: 2,
    padding: "16px",
  },
  Title: {
    fontWeight: "400",
    fontSize: "13px",
  },
  Dialog: {
    "& .MuiDialog-paper": {
      borderRadius: "12px",
    },
  },
};
