import Check from "@mui/icons-material/Check";
import Stack from "@mui/material/Stack";
import Step from "@mui/material/Step";
import StepConnector, {
  stepConnectorClasses,
} from "@mui/material/StepConnector";
import StepLabel from "@mui/material/StepLabel";
import Stepper from "@mui/material/Stepper";
import { styled } from "@mui/material/styles";
import * as React from "react";
import { Box } from "@mui/material";
import { Typography } from "@mui/material";
import LocalShippingRoundedIcon from "@mui/icons-material/LocalShippingRounded";

const QontoConnector = styled(StepConnector)(({ theme }) => ({
  [`&.${stepConnectorClasses.alternativeLabel}`]: {
    top: 10,
    left: "calc(-50% + 16px)",
    right: "calc(50% + 16px)",
  },
  [`&.${stepConnectorClasses.active}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: theme.palette.secondary.main,
    },
  },
  [`&.${stepConnectorClasses.completed}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: theme.palette.success.light,
    },
  },
  [`& .${stepConnectorClasses.line}`]: {
    borderColor: "#d3d3d3",
    borderTopWidth: 3,
    borderRadius: 1,
  },
}));

const QontoStepIconRoot = styled("div")(({ theme, ownerState }) => ({
  color: "#d3d3d3",
  display: "flex",
  height: 22,
  alignItems: "center",
  ...(ownerState.active && {
    color: theme.palette.secondary.main,
  }),
  "& .QontoStepIcon-completedIcon": {
    color: theme.palette.success.light,
    zIndex: 1,
    fontSize: 18,
  },
  "& .QontoStepIcon-circle": {
    color: "currentColor",
  },
}));

function QontoStepIcon(props) {
  const { active, completed, className } = props;

  return (
    <QontoStepIconRoot ownerState={{ active }} className={className}>
      {completed ? (
        <Check className="QontoStepIcon-completedIcon" />
      ) : (
        <LocalShippingRoundedIcon className="QontoStepIcon-circle" />
      )}
    </QontoStepIconRoot>
  );
}

export default function ToursTracking() {
  return (
    <Box sx={classes.paper}>
      <Typography sx={classes.TourIcon} className="RtoL">
        A
      </Typography>
      <Typography sx={classes.title} paragraph>
        Tours Status :
      </Typography>
      <Stack sx={classes.stack} spacing={4}>
        <Stepper alternativeLabel activeStep={2} connector={<QontoConnector />}>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel StepIconComponent={QontoStepIcon}>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
      </Stack>
    </Box>
  );
}

const classes = {
  title: {
    fontWeight: "300",
    textAlign: "center",
    opacity: 0.5,
    zIndex: 2,
  },
  TourIcon: {
    fontSize: "15rem",
    fontWeight: "900",
    position: "absolute",
    color: "secondary.main",
    opacity: ".2",
    zIndex: "1",
    right: "-2%",
    textShadow:
      "0px 0px 0 rgb(8,147,202), 1px 1px 0 rgb(-16,123,178), 2px 2px 0 rgb(-40,99,154), 3px 3px 0 rgb(-63,76,131), 4px 4px 0 rgb(-87,52,107), 5px 5px 0 rgb(-111,28,83), 6px 6px 0 rgb(-135,4,59), 7px 7px 0 rgb(-159,-20,35), 8px 8px  0 rgb(-183,-44,11), 9px 9px 8px rgba(0,0,0,0.48), 9px 9px 1px rgba(0,0,0,0.5), 0px 0px 8px rgba(0,0,0,.2)",
  },
  stack: {
    width: "100%",
    zIndex: 2,
  },
  paper: {
    backgroundColor: "#FFFFFF",
    mt: "16px",
    py: "2rem",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    borderRadius: "12px",
    boxShadow: "rgba(149, 157, 165, 0.2) 0px 8px 24px",
    position: "relative",
    overflow: "hidden",
  },
};

const steps = ["City1", "City2", "City3", "City4", "City5"];
