import MenuIcon from "@mui/icons-material/Menu";
import {
  IconButton,
  Stack,
  Slide,
  useScrollTrigger,
  styled,
} from "@mui/material";
import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { default as React, useContext } from "react";
import { useLocation } from "react-router-dom";
import man from "../../assets/pics/man.webp";
import { DraweContext, webDrawerWidth } from "./Drawer";
import MuiAppBar from "@mui/material/AppBar";

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: webDrawerWidth,
    width: `calc(100% - ${webDrawerWidth}px)`,
    [theme.breakpoints.down("sm")]: {
      width: 0,
      marginLeft: 0,
    },
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

function ElevationScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger({
    disableHysteresis: false,
    threshold: 0,
  });
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

const Header = () => {
  const { open, ToggleOpen } = useContext(DraweContext);
  const location = useLocation();
  return (
    <ElevationScroll>
      <AppBar
        position="fixed"
        elevation={0}
        open={open}
        sx={{ background: "#F9FAFB" }}
      >
        <Toolbar sx={{ paddingLeft: "0" }}>
          <IconButton
            color="primary"
            aria-label="open drawer"
            onClick={ToggleOpen}
            sx={{
              marginRight: "26px",
              marginLeft: "8px",
              ...(open && { display: "none" }),
            }}
          >
            <MenuIcon />
          </IconButton>
          <Stack direction="column" sx={{ flexGrow: 1 }}>
            <Typography variant="h1" sx={classes.HeadTitle}>
              Dashboard
            </Typography>
            <Typography sx={classes.HeadSubTitle}>
              {location.pathname === "/"
                ? "overview"
                : location.pathname.substring(1) ?? "overview"}
            </Typography>
          </Stack>
          <Box>
            <IconButton>
              <Avatar alt="Remy Sharp" src={man} />
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
    </ElevationScroll>
  );
};
const classes = {
  HeadTitle: {
    fontSize: "1.5rem",
    fontWeight: "500",
    color: "primary.main",
  },
  HeadSubTitle: {
    flexGrow: 1,
    fontSize: "1rem",
    fontWeight: "300",
    textTransform: "capitalize",
    color: "secondary.main",
  },
};
export default Header;
